.profile-card-details{
	background: #fff;
    padding: 34px 0px 0px 0px;
    border-radius: 18px;
    box-shadow: 0 5px 10px 0 rgb(204 204 204 / 16%);
    margin-bottom: 26px;
    overflow: hidden;
}

.profile-1{
	background: #ccc; color: #fff; padding: 6px;
}

.profile-pic{    
	height: 74px;
    width: 74px;
    border-radius: 60px;
    background: #ccc;
    display: inline-block;
}

.profile-1-B{
	background: #70cada;
    padding: 6px 15px;
    border-radius: 8px;
    color: #fff;
    font-size: 13px;
}

.details-dr p{
	margin-bottom: 0px;
}

.doctors-H-C{
	color: #b9b7b7;
    margin-top: 12px;
}

.font-w{font-weight: 500;}

.border-R{border-right: 1px solid #ccc; padding-right: 22px;}

.border-R-1{border-right: 1px solid #ccc; padding-right: 22px; margin-top: 32px; }

.border-R-1 p{margin-bottom: 0px;}

.border-R-2{ margin-top: 32px; }

.border-R-2 p{margin-bottom: 0px;}

.details-dr{margin-left: 16px;}

.details-1{ 
	display: flex;
}

.card-footer{ background: #266e7b; padding: 2px 8px; margin-top: 28px; color: #fff; }
.card-footer p{margin-bottom: 0px;}

.card-footer .row p{ margin: 14px 10px 0; font-size: 13px;}

.card-footer .row span{color: #71c0ce;}

.btn.view-details{
	border-radius: 20px;
	padding: 6px 18px;
	background: transparent;
	border: 1px solid #fff;
	color: #fff;
	box-shadow: none;
	font-size: 12px;
	text-transform: inherit;
}
